/* App.css */

body {
  font-family: 'Arial', sans-serif;
  background-color: #4a90e2;
  color: #333;
  margin: 0;
  padding: 0;
}

.Student_Marks {
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: rgb(251, 255, 254);
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  position: relative;
}

h1 {
  text-align: center;
  color: #4a90e2;
  margin-bottom: 1rem;
}

.reset-button {
  position: absolute;
  font-size: large;
  font-weight: bolder;
  top: 1rem;
  right: 1rem;
  padding: 0.5rem 1rem;
  background-color: #4a90e2;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.reset-button:hover {
  background-color: #b83535;
}

.Enter_Marks h2 {
  color: #333;
  margin-bottom: 1rem;
}

form {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: space-between;
}

form .column {
  flex: 1 1 45%;
}

form div {
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
}

label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

input[type="number"] {
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.btn {
  padding: 0.75rem 1.5rem;
  background-color: #4a90e2;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  align-self: center;
  margin-top: 1rem;
}

.btn:hover {
  background-color: #35b835;
}

.error {
  color: red;
  font-weight: bold;
  margin-top: 1rem;
  text-align: center;
}

.seeResults {
  margin-top: 2rem;
}

.seeResults h2 {
  text-align: center;
  color: #4a90e2;
  margin-bottom: 1rem;
}

.results-container {
  display: flex;
  justify-content: space-between;
}

.grades, .summary {
  width: 48%;
}

.grades label, .summary label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: normal;
}

.grades label::after, .summary label::after {
  content: "";
  display: block;
  margin-top: 0.25rem;
  height: 1px;
  background-color: #ccc;
}
